////
/// @group _partials/base.scss
////


// scss
// ================================

html {
	position: relative;
	height: 100%;
	font-size: 12px;
	@include media( $md ) {
		font-size: 14px;
	}
	@include media( $lg ) {
		font-size: 16px;
	}
}

*, :after, :before {
	box-sizing: inherit;
}

a {
	color: #43f;
	&:hover, &:focus{
		outline: none;
	}
}

body {
	position: relative;
	//height: 100%;
	box-sizing: border-box;
	min-width: 320px;
	font-family: "Open Sans";
}

pre {
	padding: 1.2em;
	tab-size: 4;
	border-width: 0;
	white-space: pre;
	white-space: pre-wrap;
	background: #eee;
	word-wrap: break-word;
}

img {
	max-width: 100%;
	height: auto;
}

small {
	display: inline-block;
    vertical-align: text-bottom;
}

svg {
	pointer-events: none;
}

hr {
	border-width: 0;
	border-top: 1px solid #aaa;
}

::selection {
	text-shadow:none;
	color: #fff;
	background: #2597ff;
}

::-moz-selection {
	text-shadow:none;
	color: #fff;
	background: #2597ff;
}

::-ms-selection{
	text-shadow:none;
	color: #fff;
	background: #2597ff;
}

input {
	box-shadow: none;
	@include placeholder( #999, true );
	@include placeholder-on-focus();
}

textarea {
	box-shadow: none;
	@include placeholder( #999, true );
	@include placeholder-on-focus();
}
ul{
	list-style: none;
	margin: 0;
	padding: 0;
}
h1,h2,h3,h4,h5,h6,p{
	margin: 0;
}


.container{
	margin: 0 auto;
}
.wrapper{
	margin: 0 auto;
}
.left{
	float: left;
}
.right{
	float: right;
}
section{
	overflow: hidden;
}
.clearfix{
	@include clearfix;
}
select{
	background: transparent;
	color: #999!important;
	font-size: rem(14)!important;
	border: 0;
	appearance: none;            /* remove arrow        */
	-webkit-appearance: none;    /* remove arrow Chrome */
	-moz-appearance: none;       /* remove arrow FF     */
	option{
		padding: rem(6);
	}
}
select::-ms-expand{
	display: none;             /* remove arrow IE     */
}
.control-holder{
	box-shadow: inset 2px 2px 7px rgba(36,48,55,.1);
	.select-arrow{
		width: 8px;
		height: 0px;
		background: #6C7231;
		position: absolute;
		right: 17px;
		&:before {
			content: '';
			position: absolute;
			top: -6px;
			border: 4px solid transparent;
			border-bottom-color: #6C7231;
			border-top: 0;
		}
		&:after {
			content: '';
			position: absolute;
			top: 1px;
			border: 4px solid transparent;
			border-top-color: #6C7231;
			border-bottom: 0;
		}
		@include media($st){
			top: 17px;
		}
		@include media($md){
			top: 19px;
		}
		@include media($lg){
			top: 23px;
		}

	}
}
section{
	background: #fff;
}
label.has-error{
    position: absolute;
    left: 0;
}
@include media($st){
	.container{
		width: 95%;
		.wrapper{
			width: 100%;
		}
	}
	.wrapper{
		width: 95%;
		&.drop{
			margin-left: -45%;
		}
	}
}
@include media($def){
	.container{
		width: 100%!important;
		.wrapper{
			width: 95%;
		}
	}
}
@include media($lg){
	.wrapper{
		width: 1240px!important;
		&.drop{
			margin-left: -620px;
		}
	}
}
@include media($mg){
	.container{
		width: 1440px!important;
	}
}

section, div{
	.container,.wrapper{
		h2{
			font-size: rem(42);
			line-height: em(46,42);
			font-weight: 300;
			text-align: center;
			position: relative;
			color: #3a485b;
			&.line{
				padding-bottom: rem(20);
				&:after{
					content: '';
					display: inline-block;
					position: absolute;
					bottom: 0;
					left: 50%;
					margin-left: -60px;
					width: 120px;
					height: 1px;
					background: #dcdcdc;
				}
			}
			@include media($st){

			}
		}
	}
}
